.humidity_bar_container{
  width: 229px;
}

.humidity_percentage{
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-size: .7rem;
}

.humidity_bar{
  width: 100%;
  height: 8px;
  background: #E7E7EB;
  border-radius: .5rem;
}

.humidity{
  height: 100%;
  background: #FFEC65;
  border-radius: 4px;
}